@import './../../variables.scss';

.header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    margin-bottom: 30px;
}


.wrapper {
    display: flex;
    align-items: center;
}

.change_theme,
.logo {
    margin-right: 20px;
    cursor: pointer;
}

.logo {
    svg {
        width: 65px;
        height: 65px;
    }
}

.title {
    font-size: 25px;
    font-weight: bold;
    color: $primary-color;
    text-transform: uppercase;
}

@media (max-width: 530px) {
    .header {
        flex-direction: column;
    }
}