@import "./../../variables.scss";

.this__day {
    padding: 20px;
    max-width: 400px;
    width: 100%;
    background-color: var(#{--components-background-default});
    box-shadow: $box-shadow;
    border-radius: $border-radius;
}

.top__block {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    margin-bottom: 30px;
    svg {
        width: 119px;
        height: 119px;
    }
    .this__temp {
        font-size: 96px;
        font-weight: 500;
        color: $primary-color;
    }
    .this__day_name {
        font-size: 40px;
        color: var(#{--text-color-default});
    }
}

.bottom__block {
    .this__time,
    .this__city {
        font-size: 25px;
        color: $base-color;
    }
}

@media (max-width: 1170px) {
    .this__day {
        max-width: 750px;
        padding-bottom: 35px;
    }
}